@import '../../globalVariable';

$loadingSize: 12px;

.serviceList {
    background-color: #F1F1F1;
    height: 100vh;
    .title {
        padding: 30px 0 10px 30px;
        font-size:18px;
        font-family:PingFangSC-Regular;
        font-weight:400;
        color:rgba(0,0,0,1);
        line-height:25px;
    }
    .block {
        background-color: #fff;
        margin-bottom: 12px;
        &.top {
            padding: 44px 143px 25px 144px;
        }
        .avator {
            width: 60px;
            height: 60px;
            background-color: aqua;
            border-radius: 50%;
        }
        .alias {
            margin-top: 12px;
            text-align: center;
            font-size: 19px;
            font-family: PingFangSC-Medium;
            line-height: 26px;
        }
        .line {
            height: 101px;
            margin-left: 25px;
            padding: 0 20px 0 5px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid #EFF1F3;
            font-family: PingFangSC-Regular;
            .label {
                font-size: 17px;
            }
            .value {
                font-size: 16px;
                color: #7A8289;
            }
            .leftBlock {
                display: flex;
                .avator {
                    margin-right: 20px;
                    &.evernote {
                        @include retinaBackground('../../assets/serviceIcon/evernote.png', '../../assets/serviceIcon/evernote@2x.png', '../../assets/serviceIcon/evernote@3x.png');
                    }
                    &.myyxbj {
                        @include retinaBackground('../../assets/serviceIcon/myyxbj.png', '../../assets/serviceIcon/myyxbj@2x.png', '../../assets/serviceIcon/myyxbj@3x.png');
                    }
                    &.microlist {
                        @include retinaBackground('../../assets/serviceIcon/microlist.png', '../../assets/serviceIcon/microlist@2x.png', '../../assets/serviceIcon/microlist@3x.png');
                    }
                }
                .detail {
                    display: flex;
                    flex-direction: column;
                    .name {
                        font-size:17px;
                        font-family:PingFangSC-Regular;
                        font-weight:400;
                        color:rgba(0,0,0,1);
                        line-height:24px;
                        margin-bottom: 5px;
                    }
                    .detailInfo {
                        margin-bottom: 1px;
                        font-size:11px;
                        font-family:PingFangSC-Regular;
                        font-weight:400;
                        color:rgba(122,130,137,1);
                        line-height:16px;
                    }
                }
            }
            .rightBlock {
              position: relative;
              flex-basis: 100px;  
              font-size: 14px;
              font-family: PingFangSC-Regular;
              font-weight: 400;
              line-height: 30px;

              .connectBtn {
                  width: 100px;
                  height: 30px;
                  background: none;
                  border-radius: 17px;
                  border:1px solid rgba(122,130,137,1);
                  color: #7A8289;
              }
              .direct {
                height: 10px;
                width: 10px;
                position: absolute;
                right: 5px;
                top: 10px;
                background: url('../../assets/rightArrow.png');
                background-size: contain;
                background-repeat: no-repeat;
                @media screen and (-webkit-min-device-pixel-ratio: 2) {
                    background: url('../../assets/rightArrow@2x.png');
                    background-size: contain;
                    background-repeat: no-repeat;
                } 
              }   
            }
            
            
        }
    }
   
    .desc {
        font-size:20px;
        text-align: center;
    }

    .confirmBtn {
        margin-top: 40px;
    }

    .loadingIcon {
        position: absolute;
        right: 5%;
        top: 25%;
        width: $loadingSize;
        height: $loadingSize;
        transform: translate(0, -50%);
        @include hide;
    }

}

